<template>
	<view class="jbInfo">
		<view style="width: 100%; height: 52rpx; background-color: #000;"></view>
		<view class="header">
			<view @click="bback">
				<image style="width: 40rpx; height: 40rpx; margin-top: 20rpx; margin-left: 20rpx;  " src="@/static/img/arrow-left-bold1.png"></image>
			</view>
			<view style="font-size: 38rpx; margin-left: 10upx;">基本信息</view>	
		</view>
		<view class="jbBody">
			<ul>
				<li>
					<view>手机号</view>
					<view style="display: flex; align-items: center;">
						<view v-if="phoneNum==''">无</view>
						<view v-if="phoneNum!=''">{{phoneNum}}</view>
						<image style="width: 16rpx; height: 28rpx; margin-left: 10rpx;" src="../../static/img/right.png"></image>
					</view>
				</li>
				<li>
					<view>微信</view>
					<view style="display: flex; align-items: center;">
						<image style="width: 50rpx; height: 50rpx; border-radius: 50%;" v-if="userInfo.avatar==null" :src="avatar"></image>
						<image style="width: 50rpx; height: 50rpx; border-radius: 50%;" v-else :src="userInfo.avatar"></image>
						
						<view style="margin-left: 10rpx;">{{userInfo.yhm}}</view>
						<image style="width: 16rpx; height: 28rpx; margin-left: 10rpx;" src="../../static/img/right.png"></image>
					</view>
				</li>
			</ul>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				userInfo:[],
				phoneNum:"",
				avatar:"https://mmbiz.qpic.cn/mmbiz/icTdbqWNOwNRna42FI242Lcia07jQodd2FJGIYQfG0LAJGFxM4FbnQP6yfMxBgJ0F3YRqJCJ1aPAK2dQagdusBZg/0"
			}
		},
		onShow() {
				this.phoneNum=uni.getStorageSync("phone")
				// if(this.userInfo.avatar==null){
				// 	this.userInfo.avatar="https://mmbiz.qpic.cn/mmbiz/icTdbqWNOwNRna42FI242Lcia07jQodd2FJGIYQfG0LAJGFxM4FbnQP6yfMxBgJ0F3YRqJCJ1aPAK2dQagdusBZg/0"
				// }
				uni.request({
					method:'POST',
					url:this.apiUrl+"index.php/index/jbxx",
					data:JSON.stringify({
						"openid":uni.getStorageSync("openid")
					}),
					success: (res) => {
						console.log(res)	
						this.userInfo = res.data
					}
				})			
		},
		methods: {
			bback(){
				uni.navigateBack(-1)
			}
		}
	}
</script>

<style>
.jbInfo .header{
	width: 100%;
	height: 120rpx;	
	background-color: #000;
	color: #fff;
	display: flex;
	align-items: center;
}
.jbInfo .jbBody{
	width: 90%;
	margin: 20rpx auto;
}
.jbInfo .jbBody ul li{
	width: 100%;
	display: flex;
	align-items: center;
	height: 100rpx;
	justify-content: space-between;
	border-bottom: 2rpx solid #e5e5e5;
}
</style>
